.chat{
    flex:0.65;
    display: flex;
    flex-direction: column;
    
}
.chat_header{
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}
.chat_headerInfo{
    flex:1;
    padding-left: 20px;
}
.chat_headerInfo>h3{
    margin-bottom: 3px;
    font-weight: 500;

}
.chat_headerInfo>p{
    color: grey;
}

.chat_headerRight{
    display: flex;
    justify-content: space-between;
    min-width: 100px;
    /* border: 1px solid black; */
    
}
.chat_headerRight>.MuiIconButton-root {
padding: 5px;
}
.chat_body{
    flex:1;
    background-image: url("https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png");
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: auto;
    display: flex;
    flex-direction: column-reverse;

}
.chat_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}
.chat_footer>form{
    flex:1;
    display: flex;


}
.chat_footer >form>input{
    flex:1;
    border-radius: 30px;
    padding :10px;
    border:none;
    outline-width: 0px;
}
.chat_footer >form >button{
    display:none;
}
.chat_footer >.MuiSvgIcon-root{
    padding: 10px;
    color: grey;

}